"use client";

import { useState, useRef, useEffect } from "react";
import type { ChatResponse } from "../hooks/useWebSocket";

type ChatSidebarProps = {
  onSendMessage: (query: string) => void;
  lastResponse: ChatResponse | null;
  isConnected: boolean;
};

type Message = {
  id: string;
  type: "user" | "assistant";
  content: string;
  citations?: string[];
  timestamp: number;
};

export function ChatSidebar({ onSendMessage, lastResponse, isConnected }: ChatSidebarProps) {
  const [input, setInput] = useState("");
  const [messages, setMessages] = useState<Message[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const messagesEndRef = useRef<HTMLDivElement>(null);
  
  // Scroll to bottom when new messages arrive
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);
  
  // Handle incoming response
  useEffect(() => {
    if (lastResponse) {
      setMessages((prev) => [
        ...prev,
        {
          id: lastResponse.message_id,
          type: "assistant",
          content: lastResponse.response,
          citations: lastResponse.citations,
          timestamp: lastResponse.timestamp,
        },
      ]);
      setIsLoading(false);
    }
  }, [lastResponse]);
  
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (!input.trim() || !isConnected || isLoading) return;
    
    const query = input.trim();
    
    // Add user message
    setMessages((prev) => [
      ...prev,
      {
        id: `user_${Date.now()}`,
        type: "user",
        content: query,
        timestamp: Date.now(),
      },
    ]);
    
    // Send to backend
    onSendMessage(query);
    setInput("");
    setIsLoading(true);
  };
  
  const suggestedQueries = [
    "Why is TRK-102 flagged?",
    "Which trucks exceeded quota?",
    "What are BS-VI idle limits?",
  ];
  
  return (
    <div className="flex flex-col h-full" style={{ background: "var(--bg-elevated)" }}>
      {/* Header */}
      <div 
        className="p-3 flex items-center justify-between"
        style={{ borderBottom: "var(--border-brutal)" }}
      >
        <h3 className="text-sm font-bold" style={{ color: "var(--text-primary)" }}>
          üí¨ ASK PATHGREEN
        </h3>
        <div 
          className="mono"
          style={{ 
            fontSize: "10px", 
            color: isConnected ? "var(--accent-green)" : "var(--text-muted)" 
          }}
        >
          {isConnected ? "‚óè ONLINE" : "‚óã OFFLINE"}
        </div>
      </div>
      
      {/* Messages area */}
      <div 
        className="flex-1 overflow-y-auto p-3"
        style={{ background: "var(--bg-primary)" }}
      >
        {messages.length === 0 ? (
          <div className="flex flex-col gap-4">
            <p 
              className="mono text-center"
              style={{ fontSize: "12px", color: "var(--text-muted)" }}
            >
              Ask about fleet emissions, alerts, or BS-VI regulations.
            </p>
            
            {/* Suggested queries */}
            <div className="flex flex-col gap-2">
              {suggestedQueries.map((query) => (
                <button
                  key={query}
                  className="p-2 text-left transition-colors mono"
                  style={{
                    fontSize: "11px",
                    background: "var(--bg-surface)",
                    border: "1px solid var(--border-color)",
                    color: "var(--text-secondary)",
                    cursor: "pointer",
                  }}
                  onClick={() => {
                    setInput(query);
                  }}
                  onMouseEnter={(e) => {
                    e.currentTarget.style.borderColor = "var(--accent-blue)";
                    e.currentTarget.style.color = "var(--text-primary)";
                  }}
                  onMouseLeave={(e) => {
                    e.currentTarget.style.borderColor = "var(--border-color)";
                    e.currentTarget.style.color = "var(--text-secondary)";
                  }}
                >
                  {query}
                </button>
              ))}
            </div>
          </div>
        ) : (
          <div className="flex flex-col gap-3">
            {messages.map((msg) => (
              <div
                key={msg.id}
                className={`p-3 ${msg.type === "user" ? "ml-4" : "mr-4"}`}
                style={{
                  background: msg.type === "user" ? "var(--bg-surface)" : "var(--bg-elevated)",
                  border: msg.type === "user" 
                    ? "1px solid var(--accent-blue)" 
                    : "var(--border-brutal)",
                }}
              >
                {/* Message header */}
                <div 
                  className="flex items-center justify-between mb-2"
                  style={{ fontSize: "10px", color: "var(--text-muted)" }}
                >
                  <span className="mono uppercase font-bold">
                    {msg.type === "user" ? "You" : "PathGreen AI"}
                  </span>
                </div>
                
                {/* Message content */}
                <div 
                  className="mono prose prose-invert prose-sm max-w-none"
                  style={{ 
                    fontSize: "12px", 
                    color: "var(--text-primary)",
                    lineHeight: 1.6,
                    whiteSpace: "pre-wrap",
                  }}
                  dangerouslySetInnerHTML={{ 
                    __html: formatMarkdown(msg.content) 
                  }}
                />
                
                {/* Citations */}
                {msg.citations && msg.citations.length > 0 && (
                  <div 
                    className="mt-2 pt-2"
                    style={{ borderTop: "1px solid var(--border-color)" }}
                  >
                    <span 
                      className="mono"
                      style={{ fontSize: "10px", color: "var(--text-muted)" }}
                    >
                      Sources: {msg.citations.join(", ")}
                    </span>
                  </div>
                )}
              </div>
            ))}
            
            {/* Loading indicator */}
            {isLoading && (
              <div 
                className="p-3 mr-4"
                style={{ 
                  background: "var(--bg-elevated)", 
                  border: "var(--border-brutal)" 
                }}
              >
                <div className="flex items-center gap-2">
                  <div className="flex gap-1">
                    <span 
                      className="w-2 h-2 rounded-full animate-pulse"
                      style={{ background: "var(--accent-green)", animationDelay: "0ms" }}
                    />
                    <span 
                      className="w-2 h-2 rounded-full animate-pulse"
                      style={{ background: "var(--accent-green)", animationDelay: "150ms" }}
                    />
                    <span 
                      className="w-2 h-2 rounded-full animate-pulse"
                      style={{ background: "var(--accent-green)", animationDelay: "300ms" }}
                    />
                  </div>
                  <span className="mono" style={{ fontSize: "11px", color: "var(--text-muted)" }}>
                    Analyzing...
                  </span>
                </div>
              </div>
            )}
            
            <div ref={messagesEndRef} />
          </div>
        )}
      </div>
      
      {/* Input area */}
      <form 
        onSubmit={handleSubmit}
        className="p-3"
        style={{ borderTop: "var(--border-brutal)" }}
      >
        <div className="flex gap-2">
          <input
            type="text"
            value={input}
            onChange={(e) => setInput(e.target.value)}
            placeholder={isConnected ? "Ask about fleet emissions..." : "Connecting..."}
            disabled={!isConnected || isLoading}
            className="input flex-1"
            style={{ fontSize: "12px" }}
          />
          <button
            type="submit"
            disabled={!isConnected || isLoading || !input.trim()}
            className="btn btn--primary"
            style={{ 
              padding: "8px 16px",
              opacity: (!isConnected || isLoading || !input.trim()) ? 0.5 : 1,
            }}
          >
            ‚Üí
          </button>
        </div>
      </form>
    </div>
  );
}

// Simple markdown to HTML conversion
function formatMarkdown(text: string): string {
  return text
    // Bold
    .replace(/\*\*(.*?)\*\*/g, "<strong>$1</strong>")
    // Headers
    .replace(/^### (.*$)/gm, "<h4 style='margin: 8px 0 4px; font-size: 12px;'>$1</h4>")
    .replace(/^## (.*$)/gm, "<h3 style='margin: 8px 0 4px; font-size: 13px;'>$1</h3>")
    // Lists
    .replace(/^- (.*$)/gm, "<li style='margin-left: 16px;'>$1</li>")
    .replace(/^‚Ä¢ (.*$)/gm, "<li style='margin-left: 16px;'>$1</li>")
    // Table rows (simple)
    .replace(/\|(.*)\|/g, (match) => {
      const cells = match.split("|").filter(Boolean);
      return `<div style="display: flex; gap: 8px; font-size: 11px; padding: 2px 0; border-bottom: 1px solid var(--border-color);">${cells.map(c => `<span style="flex: 1;">${c.trim()}</span>`).join("")}</div>`;
    })
    // Line breaks
    .replace(/\n/g, "<br />");
}
