"use client";

import type { EmissionData } from "../hooks/useWebSocket";

type EmissionGaugesProps = {
  vehicles: EmissionData[];
  selectedVehicle?: string | null;
  onVehicleSelect?: (vehicleId: string) => void;
};

// Daily carbon quota per vehicle (kg)
const DAILY_QUOTA_KG = 80;

function getStatusColor(vehicle: EmissionData): string {
  if (vehicle.alert_severity === "CRITICAL") return "var(--accent-red)";
  if (vehicle.alert_severity === "WARNING") return "var(--accent-amber)";
  return "var(--accent-green)";
}

function getQuotaPercentage(cumulativeCo2Kg: number): number {
  return Math.min(100, (cumulativeCo2Kg / DAILY_QUOTA_KG) * 100);
}

function getQuotaColor(percentage: number): string {
  if (percentage >= 90) return "var(--accent-red)";
  if (percentage >= 70) return "var(--accent-amber)";
  return "var(--accent-green)";
}

export function EmissionGauges({ vehicles, selectedVehicle, onVehicleSelect }: EmissionGaugesProps) {
  // Calculate fleet-wide stats
  const totalCo2Kg = vehicles.reduce((sum, v) => sum + v.cumulative_co2_kg, 0);
  const fleetQuota = DAILY_QUOTA_KG * vehicles.length;
  const fleetPercentage = fleetQuota > 0 ? (totalCo2Kg / fleetQuota) * 100 : 0;
  
  const criticalCount = vehicles.filter(v => v.alert_severity === "CRITICAL").length;
  const warningCount = vehicles.filter(v => v.alert_severity === "WARNING").length;
  
  return (
    <div className="flex flex-col gap-4 p-4 h-full overflow-y-auto">
      {/* Fleet-wide gauge */}
      <div className="card-surface">
        <div className="flex items-center justify-between mb-2">
          <h4 className="text-sm font-bold" style={{ color: "var(--text-primary)" }}>
            FLEET CARBON QUOTA
          </h4>
          <span className="mono" style={{ fontSize: "12px", color: "var(--text-muted)" }}>
            {vehicles.length} vehicles
          </span>
        </div>
        
        {/* Progress bar */}
        <div 
          className="w-full h-6 mb-2"
          style={{ background: "var(--bg-primary)", border: "1px solid var(--border-color)" }}
        >
          <div
            className="h-full transition-all"
            style={{
              width: `${Math.min(100, fleetPercentage)}%`,
              background: getQuotaColor(fleetPercentage),
            }}
          />
        </div>
        
        <div className="flex justify-between mono" style={{ fontSize: "12px" }}>
          <span style={{ color: getQuotaColor(fleetPercentage) }}>
            {totalCo2Kg.toFixed(1)} kg
          </span>
          <span style={{ color: "var(--text-muted)" }}>
            /{fleetQuota} kg ({fleetPercentage.toFixed(0)}%)
          </span>
        </div>
        
        {/* Alert summary */}
        {(criticalCount > 0 || warningCount > 0) && (
          <div className="flex gap-4 mt-3 pt-3" style={{ borderTop: "1px solid var(--border-color)" }}>
            {criticalCount > 0 && (
              <div className="flex items-center gap-2">
                <span className="status-dot status-dot--red"></span>
                <span className="mono" style={{ fontSize: "12px", color: "var(--accent-red)" }}>
                  {criticalCount} Critical
                </span>
              </div>
            )}
            {warningCount > 0 && (
              <div className="flex items-center gap-2">
                <span className="status-dot status-dot--amber"></span>
                <span className="mono" style={{ fontSize: "12px", color: "var(--accent-amber)" }}>
                  {warningCount} Warning
                </span>
              </div>
            )}
          </div>
        )}
      </div>
      
      {/* Per-vehicle stats */}
      <div className="flex flex-col gap-2">
        <h4 
          className="text-xs font-bold uppercase"
          style={{ color: "var(--text-muted)", letterSpacing: "0.1em" }}
        >
          Vehicle Status
        </h4>
        
        {vehicles.map((vehicle) => {
          const quotaPercent = getQuotaPercentage(vehicle.cumulative_co2_kg);
          const isSelected = vehicle.vehicle_id === selectedVehicle;
          const statusColor = getStatusColor(vehicle);
          
          return (
            <div
              key={vehicle.vehicle_id}
              className="p-2 cursor-pointer transition-all"
              style={{
                background: isSelected ? "var(--bg-surface)" : "transparent",
                border: isSelected ? "2px solid var(--accent-blue)" : "1px solid var(--border-color)",
              }}
              onClick={() => onVehicleSelect?.(vehicle.vehicle_id)}
              onMouseEnter={(e) => {
                if (!isSelected) e.currentTarget.style.background = "var(--bg-hover)";
              }}
              onMouseLeave={(e) => {
                if (!isSelected) e.currentTarget.style.background = "transparent";
              }}
            >
              <div className="flex items-center justify-between mb-1">
                <div className="flex items-center gap-2">
                  <span 
                    className="status-dot"
                    style={{
                      background: statusColor,
                      boxShadow: vehicle.alert_severity !== "INFO" 
                        ? `0 0 8px ${statusColor}` 
                        : "none",
                    }}
                  ></span>
                  <span className="mono font-bold" style={{ fontSize: "13px" }}>
                    {vehicle.vehicle_id}
                  </span>
                </div>
                <span 
                  className="mono font-bold"
                  style={{ fontSize: "13px", color: statusColor }}
                >
                  {vehicle.co2_rate_g_per_km > 0 
                    ? `${vehicle.co2_rate_g_per_km.toFixed(0)} g/km`
                    : "IDLE"
                  }
                </span>
              </div>
              
              {/* Mini progress bar */}
              <div 
                className="w-full h-1"
                style={{ background: "var(--bg-primary)" }}
              >
                <div
                  className="h-full"
                  style={{
                    width: `${quotaPercent}%`,
                    background: getQuotaColor(quotaPercent),
                  }}
                />
              </div>
              
              <div className="flex justify-between mt-1 mono" style={{ fontSize: "10px", color: "var(--text-muted)" }}>
                <span>{vehicle.speed_kmh.toFixed(0)} km/h</span>
                <span>{vehicle.cumulative_co2_kg.toFixed(2)} kg</span>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
}
