"use client";

import { useState } from "react";
import dynamic from "next/dynamic";
import { useWebSocket } from "./hooks/useWebSocket";
import { AlertFeed } from "./components/AlertFeed";
import { EmissionGauges } from "./components/EmissionGauges";
import { ChatSidebar } from "./components/ChatSidebar";

// Dynamic import for map (requires window)
const FleetMap = dynamic(
  () => import("./components/FleetMap").then((mod) => mod.FleetMap),
  { 
    ssr: false,
    loading: () => (
      <div 
        className="w-full h-full flex items-center justify-center"
        style={{ background: "var(--bg-void)" }}
      >
        <div className="mono" style={{ color: "var(--text-muted)" }}>
          Loading map...
        </div>
      </div>
    ),
  }
);

export default function Dashboard() {
  const { 
    status, 
    vehicles, 
    alerts, 
    lastChatResponse, 
    sendChatMessage 
  } = useWebSocket();
  
  const [selectedVehicle, setSelectedVehicle] = useState<string | null>(null);
  const [activePanel, setActivePanel] = useState<"gauges" | "alerts">("gauges");
  
  const isConnected = status === "connected";
  
  return (
    <div className="dashboard-grid">
      {/* ================================================================
          HEADER
          ================================================================ */}
      <header 
        className="header col-span-2"
        style={{ gridColumn: "1 / -1" }}
      >
        <div className="flex items-center gap-4">
          {/* Logo */}
          <h1 
            className="text-xl font-bold tracking-tight"
            style={{ color: "var(--accent-green)" }}
          >
            PATHGREEN<span style={{ color: "var(--text-primary)" }}>-AI</span>
          </h1>
          
          {/* Stats */}
          <div className="flex items-center gap-4 ml-8">
            <div className="mono" style={{ fontSize: "12px", color: "var(--text-secondary)" }}>
              <span style={{ color: "var(--text-primary)", fontWeight: 600 }}>
                {vehicles.length}
              </span>
              {" "}Vehicles
            </div>
            <div className="mono" style={{ fontSize: "12px", color: "var(--text-secondary)" }}>
              <span style={{ color: alerts.filter(a => a.severity === "CRITICAL").length > 0 ? "var(--accent-red)" : "var(--text-primary)", fontWeight: 600 }}>
                {alerts.filter(a => a.severity === "CRITICAL" || a.severity === "WARNING").length}
              </span>
              {" "}Alerts
            </div>
          </div>
        </div>
        
        {/* Connection status */}
        <div className="live-indicator">
          {isConnected ? (
            <>
              <span className="live-indicator__dot"></span>
              <span style={{ color: "var(--accent-green)" }}>LIVE</span>
            </>
          ) : (
            <>
              <span 
                className="w-2 h-2 rounded-full"
                style={{ background: "var(--accent-amber)" }}
              ></span>
              <span style={{ color: "var(--accent-amber)" }}>
                {status === "connecting" ? "CONNECTING..." : "DISCONNECTED"}
              </span>
            </>
          )}
        </div>
      </header>
      
      {/* ================================================================
          MAIN CONTENT - Map
          ================================================================ */}
      <main 
        className="relative"
        style={{ 
          gridColumn: "1", 
          gridRow: "2 / 4",
          minHeight: 0,
        }}
      >
        <FleetMap
          vehicles={vehicles}
          selectedVehicle={selectedVehicle}
          onVehicleSelect={setSelectedVehicle}
        />
        
        {/* Map overlay - selected vehicle info */}
        {selectedVehicle && (
          <div 
            className="absolute top-4 left-4 p-4 card z-[1000]"
            style={{ minWidth: "200px" }}
          >
            <div className="flex items-center justify-between mb-2">
              <h3 className="text-sm font-bold">{selectedVehicle}</h3>
              <button
                className="mono text-xs"
                style={{ color: "var(--text-muted)", cursor: "pointer" }}
                onClick={() => setSelectedVehicle(null)}
              >
                ‚úï
              </button>
            </div>
            {(() => {
              const vehicle = vehicles.find(v => v.vehicle_id === selectedVehicle);
              if (!vehicle) return null;
              return (
                <div className="mono" style={{ fontSize: "11px" }}>
                  <div className="flex justify-between mb-1">
                    <span style={{ color: "var(--text-muted)" }}>Position</span>
                    <span>{vehicle.latitude.toFixed(4)}¬∞N, {vehicle.longitude.toFixed(4)}¬∞E</span>
                  </div>
                  <div className="flex justify-between mb-1">
                    <span style={{ color: "var(--text-muted)" }}>Speed</span>
                    <span>{vehicle.speed_kmh.toFixed(1)} km/h</span>
                  </div>
                  <div className="flex justify-between">
                    <span style={{ color: "var(--text-muted)" }}>CO‚ÇÇ Rate</span>
                    <span style={{ 
                      color: vehicle.alert_severity === "CRITICAL" ? "var(--accent-red)" :
                             vehicle.alert_severity === "WARNING" ? "var(--accent-amber)" : 
                             "var(--accent-green)"
                    }}>
                      {vehicle.co2_rate_g_per_km.toFixed(0)} g/km
                    </span>
                  </div>
                </div>
              );
            })()}
          </div>
        )}
      </main>
      
      {/* ================================================================
          SIDEBAR - Gauges/Alerts + Chat
          ================================================================ */}
      <aside className="sidebar" style={{ gridColumn: "2", gridRow: "2 / 4" }}>
        {/* Panel toggle */}
        <div 
          className="flex"
          style={{ borderBottom: "var(--border-brutal)" }}
        >
          <button
            className={`flex-1 py-2 px-4 mono text-xs uppercase font-bold transition-colors`}
            style={{
              background: activePanel === "gauges" ? "var(--bg-surface)" : "transparent",
              color: activePanel === "gauges" ? "var(--text-primary)" : "var(--text-muted)",
              borderRight: "1px solid var(--border-color)",
            }}
            onClick={() => setActivePanel("gauges")}
          >
            Emissions
          </button>
          <button
            className={`flex-1 py-2 px-4 mono text-xs uppercase font-bold transition-colors relative`}
            style={{
              background: activePanel === "alerts" ? "var(--bg-surface)" : "transparent",
              color: activePanel === "alerts" ? "var(--text-primary)" : "var(--text-muted)",
            }}
            onClick={() => setActivePanel("alerts")}
          >
            Alerts
            {alerts.filter(a => a.severity === "CRITICAL").length > 0 && (
              <span 
                className="absolute top-1 right-2 w-2 h-2 rounded-full"
                style={{ background: "var(--accent-red)" }}
              />
            )}
          </button>
        </div>
        
        {/* Panel content */}
        <div className="flex-1 overflow-hidden">
          {activePanel === "gauges" ? (
            <EmissionGauges
              vehicles={vehicles}
              selectedVehicle={selectedVehicle}
              onVehicleSelect={setSelectedVehicle}
            />
          ) : (
            <AlertFeed
              alerts={alerts}
              onAlertClick={(alert) => setSelectedVehicle(alert.vehicle_id)}
            />
          )}
        </div>
        
        {/* Chat - always visible at bottom */}
        <div 
          style={{ 
            height: "280px", 
            borderTop: "var(--border-brutal)",
            flexShrink: 0,
          }}
        >
          <ChatSidebar
            onSendMessage={sendChatMessage}
            lastResponse={lastChatResponse}
            isConnected={isConnected}
          />
        </div>
      </aside>
    </div>
  );
}
